import React from "react";

import Benevolent from "blogPosts/researchPosts/Benevolent.js";
import Aggression from "blogPosts/researchPosts/Aggression.js";
import Triage from "blogPosts/researchPosts/Triage.js";

class ResearchWrapper extends React.Component {

  render(){

    const component = this.props.match.params.work;
    if(component === "benevolent"){
      return <Benevolent />
    }
    else if(component === "aggression"){
      return <Aggression />
    }
    else {
      return <Triage />
    }

  }
}

export default ResearchWrapper;
